.container{display:flex;justify-content:center;align-items:center;text-align:center;padding:20px;font-family:Arial,sans-serif;background-image:url("../../assets/picture/BG/背景-防空洞.png");background-size:cover;background-position:center;height:100vh;width:100vw}.content{display:flex;flex-direction:column;align-items:center;max-width:90%;margin:0 auto 20px;font-size:2.5vw;color:#fff;font-family:Arial,Helvetica,sans-serif;font-weight:bold}.button{padding:1em 2em;font-size:1.5vw;background-color:#4caf50;color:#fff;border:none;border-radius:5px;cursor:pointer;margin-top:1.5rem;transition:background-color .3s ease}.button:hover{background-color:#45a049}.input{padding:.8em;font-size:3vw;width:100%;max-width:400px;margin-bottom:20px}@media screen and (max-width: 768px){.content{font-size:4vw}.input{font-size:5vw}.button{font-size:3vw}}