.container1 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    position: relative;
    overflow: hidden;
    background: #000;
    color: #fff;
    text-align: center;
}

button.button {
    padding: 10px 20px;
    font-size: 16px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 100;
    transition: background-color 0.3s ease;
    white-space: nowrap;

    &:hover {
        background-color: #45a049;
    }
}

.hide-background {
    background-color: black !important;
}

/* 使用媒體查詢來適應不同的螢幕大小 */
@media (max-width: 768px) {
    button.button {
        font-size: 14px;
        padding: 8px 16px;
    }
}

@media (max-width: 480px) {
    button.button {
        font-size: 12px;
        padding: 6px 12px;
    }
}