*{margin:0;padding:0;box-sizing:border-box}html,body{width:100vw;height:100vh;margin:0;padding:0;overflow:hidden}body{background-image:url(./assets/picture/BG/背景-活動中心.png);background-size:cover;background-repeat:no-repeat;background-position:center;display:flex;justify-content:center;align-items:center;flex-direction:column;overflow:hidden}.container,.centered-container{display:flex;justify-content:center;align-items:center;flex-direction:column;text-align:center;width:100%;height:100%}.dialog{display:flex;justify-content:center;align-items:center;flex-direction:column;width:100%;height:100%;position:relative}.centered-image{width:100%;height:100%;display:flex;justify-content:center;align-items:center;background-size:cover;background-position:center;position:relative}.foundButton{width:20vw;height:15vh;display:flex;justify-content:center;align-items:center;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);margin:0;padding:0;color:#fff;border:none;border-radius:10px;cursor:pointer;transition:background-color .3s,transform .2s}.foundButton:active{background-color:#45a049;transform:translate(-50%, -50%) scale(0.95)}@media screen and (max-width: 768px){html{transform:none;transform-origin:center center;overflow:hidden}}@media screen and (orientation: portrait){html,body{transform:none;width:100%;height:100%}}