.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-size: cover;
    background-position: center;
    padding: 20px;
}

.mapContainer {
    display: flex;
    align-items: center;
    justify-content: center;
}

.audioButtonsContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 20px;
}

.mapImageContainer {
    position: relative;
    display: flex;
    align-items: center;
}

.mapImage {
    width: 100%;
    max-width: 600px;
    height: auto;
}

.map-toggle-button {
    padding: 10px;
    font-size: 16px;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    position: absolute;
    right: 10px; // 調整到右邊
    top: 50%; // 垂直居中
    transform: translateY(-50%);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color 0.3s ease;

    &:hover {
        background-color: #45a049;
    }

    svg {
        font-size: 18px;
    }
}

.buttonsContainer {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
    gap: 10px;
    flex-wrap: wrap;
}

.audio-button {
    padding: 10px 20px;
    font-size: 16px;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    white-space: nowrap;
    transition: background-color 0.3s ease;

    &:hover {
        background-color: #45a049;
    }
}

.resultContainer {
    margin-top: 20px;
    display: flex;
    flex-direction: row;
    /* 水平排列 */
    align-items: center;
    gap: 10px;
}

.input {
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
    width: 80%;
    max-width: 300px;
}

.end-button {
    padding: 10px 20px;
    font-size: 16px;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    white-space: nowrap;
    transition: background-color 0.3s ease;

    &:hover {
        background-color: #45a049;
    }
}

@media (max-width: 768px) {
    .mapImage {
        width: 80%;
    }

    .map-toggle-button,
    .audio-button {
        padding: 8px 16px;
        font-size: 14px;
    }
}

@media (max-width: 480px) {
    .mapImage {
        width: 100%;
    }

    .map-toggle-button,
    .audio-button {
        padding: 6px 12px;
        font-size: 12px;
    }

    .input {
        width: 100%;
    }
}